import React, { useState } from "react";
import "./Login.css"
import postLogin from "../../connection/postLogin";
import getData from "../../connection/getData";

const Login = ({ setLogin }) => {
    const [message, setMessage] = useState(false);
    function onLogin(event) {
        event.preventDefault();
        const username = document.getElementById("login_username").value;
        const password = document.getElementById("login_password").value;
        async function loginNow() {
            const data = await postLogin(username, password);
            if (data.success === true) {
                setLogin(true);
            }
            else {
                setMessage(data.message);
            }
            getData(setLogin);
        }
        loginNow()

    }

    return (

        <main class="form-signin w-100 m-auto">
            <div className="card">
                <div className="card-body">
                    <form onSubmit={onLogin}>
                        <img class="mb-4" src="https://upload.wikimedia.org/wikipedia/commons/thumb/a/ab/ICT_Academy_Kerala.webp/180px-ICT_Academy_Kerala.webp.png" alt="" width="72" height="57" />
                        <h1 class="h3 mb-3 fw-normal">Please sign in</h1>
                        {message ? (<div class="alert alert-danger" role="alert">
                           {message}
                        </div>) : ""}
                        <div class="form-floating">
                            <input type="text" class="form-control" id="login_username" placeholder="user" required />
                            <label for="floatingInput">Username</label>
                        </div>
                        <div class="form-floating">
                            <input type="password" class="form-control" id="login_password" placeholder="Password" required />
                            <label for="floatingPassword">Password</label>
                        </div>
                        <button class="w-100 btn btn-lg btn-primary" type="submit"  >Sign in</button>
                    </form>
                </div>

            </div>

        </main>
    )
}

export default Login;