import React, { useState } from 'react';
import './Table.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFileDownload } from '@fortawesome/free-solid-svg-icons';
import 'bootstrap/dist/css/bootstrap.min.css';

const Table = ({ data }) => {
  const south = require("../../../components/MyForm/data/southcollege.json");
  const north = require("../../../components/MyForm/data/northcollege.json");
  const presouth = require("../../../components/MyForm/data/presouthcollege.json");
  const prenorth = require("../../../components/MyForm/data/prenorthcollege.json");
  const collegeList = south.concat(north).concat(prenorth).concat(presouth);
  collegeList.sort()
  const [searchTerm, setSearchTerm] = useState('');
  const [filterTerm, setFilterTerm] = useState('');
  const [collegeFilterTerm, setCollegeFilterTerm] = useState('');

  const collegeFilteredData = data.filter(item =>
    Object.values(item).some(value =>
      value.toString().toLowerCase().includes(collegeFilterTerm.toLowerCase())
    )
  );

  const filteredData = collegeFilteredData.filter(item =>
    Object.values(item).some(value =>
      value.toString().toLowerCase().includes(filterTerm.toLowerCase())
    )
  );

  const searchedData = filteredData.filter(item =>
    Object.values(item).some(value =>
      value.toString().toLowerCase().includes(searchTerm.toLowerCase())
    )
  );

  const convertToCSV = () => {
    // Adding header row to CSV content
    const headerRow = Object.keys(searchedData[0]).join(',');
    const csvRows = searchedData.map(row => {
      const values = Object.entries(row).map(([key, value]) => {
        // Convert "Date and Time" to localized string
        if (key === 'Date and Time') {
          return new Date(value);
        }
        return value;
      });
      return values.join(',');
    });

    const csvContent = "data:text/csv;charset=utf-8," + headerRow + '\n' + csvRows.join('\n');
    const encodedUri = encodeURI(csvContent);
    const link = document.createElement("a");
    link.setAttribute("href", encodedUri);
    link.setAttribute("download", "data.csv");
    document.body.appendChild(link);
    link.click();
  }

  return (
    <div className='col-12'>
      <div className="card">
        <div className="card-header card-header-custom">
          <div className='row'>
            <div className='col-5'>
              <h3>Student List</h3>
            </div>
            <div className='col-2'>
              <select className="form-select mb-3" aria-label="Large select example" onChange={(e) => setCollegeFilterTerm(e.target.value)}>
                <option selected value="">Filter by College</option>
                {collegeList.map((singleCollege) => (
                  <option key={singleCollege} value={singleCollege}>{singleCollege}</option>
                ))
                }
              </select>
            </div>
            <div className='col-2'>
              <select className="form-select mb-3" aria-label="Large select example" onChange={(e) => setFilterTerm(e.target.value)}>
                <option selected value="">Filter by</option>
                <option value="Thiruvananthapuram">Thiruvananthapuram</option>
                <option value="Kozhikode">Kozhikode</option>
              </select>
            </div>
            <div className='col-2'>
              <form className="d-flex">
                <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" onChange={(e) => setSearchTerm(e.target.value)} />
              </form>
            </div>
            <div className='col-1'>
              <button type="button" className="btn btn-primary" onClick={convertToCSV}><FontAwesomeIcon icon={faFileDownload} /></button>
            </div>
          </div>
        </div>
        <div className="card-body">
          {searchedData.length > 0 ? (
            <div className='table-responsive'>
              <table className="table">
                <thead>
                  <tr>
                    {Object.keys(searchedData[0]).map((key, index) => (
                      <th key={index}>{key}</th>
                    ))}
                  </tr>
                </thead>
                <tbody>
                  {searchedData.map((item, index) => (
                    <tr key={index}>
                      {Object.entries(item).map(([key, value], index) => (
                        <td key={index}>
                          {key === "Date and Time" ? new Date(value).toLocaleString() : value}
                        </td>
                      ))}
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ) : (
            <p>No data found</p>
          )}
        </div>
      </div>
    </div>
  );
};

export default Table;
