import React, { useEffect, useState } from 'react';
import RegisterCard from './RegisterCard';
import Table from './Table';
import getData from '../connection/getData';


const Main =({setLogin}) => {
  const [data,setData] =useState(false);
  const [tvmconut,setTvmCount]=useState(false);
  const [cltconut,setCltCount] = useState(false)
  const [total,setTotalCount] = useState(false)
  useEffect(()=>{
    async function getallData(){
      const here = await getData(setLogin);
      if(here === 401)
      {
       
        setLogin(false);
        await localStorage.removeItem('token'); 
      }
      else if(here === 404){
        setLogin(false);
        await localStorage.removeItem('token'); 
      }
      else{
        setData(here.data.student);
      setTvmCount(here.data.tvmCount);
      setCltCount(here.data.cltCount);
      setTotalCount(here.data.totalCount);
      }
      
    }
    getallData()
  },[setLogin]);
  
  return (
    <>
    {data ? (<div className="container">
    <div className="row">
        <RegisterCard name="Kozhikode" number={cltconut}/>
        <RegisterCard name="Thiruvananthapuram" number={tvmconut}/>
        <RegisterCard name="Total" number={total}/>
    </div>
    <br/>
    <div className='row'>
      <Table data={data} />
    </div>
    <div className='row'>
      {/* <DonutChart data={student_data} /> */}
    </div>
  </div>) :<>loading data</> }
    </>
    
    
  );
};

export default Main;

