import React from 'react';

const NavBar = ({setLogin}) => {
  const logout = ()=>{
    const logout_user= async()=>{
      await localStorage.removeItem('token'); // Remove 'token' from localStorage
      setLogin(false);
    }
    logout_user();
  }
  return (
    <nav class="navbar navbar-light bg-light">
      <a class="navbar-brand" href='/admin'>
        <img src="https://ictkerala.org/assets/images/LOGO_ICTAK-Name.svg" width="100%" height="50" className="d-inline-block align-top" alt="" />
      </a>
      <form class="form-inline">
        <button class="btn btn-danger my-2 my-sm-0" type="button" style={{margin:"10px"}} onClick={logout}>Logout</button>
      </form>
    </nav>
  );
};
export default NavBar;
